<template>
  <div id="her">
        <div v-if="FlagCity" class="con">
            <!-- 左边 -->
            <div class="her1">ele.me</div>
            <!-- 中间 -->
            <div class="her2">{{this.msg}}</div>
            <!-- 右边 -->
            <div class="her3" @click="$router.push('/Signin')">
                <span>登录</span>
                <span>|</span>
                <span>注册</span>
            </div>
        </div>
        <div v-if="Flagadd" class="con">
            <!-- 左边 -->
            <div class="her1" @click="$router.push('/')">
                <van-icon name="arrow-left" />
            </div>
            <!-- 中间 -->
            <div class="her4">
                {{this.$route.query.name}}
               
            </div>
            <!-- 右边 -->
            <div class="her3" @click="$router.push('/')">切换城市</div>
        </div>
        <div v-if="FlagHome" class="con" style="justify-content: space-between">
            <!-- 左边 -->
            <div class="her1" @click="$router.push('/Search')">
                <van-icon name="search" />
            </div>
            <!-- 中间 -->
            <div class="her2">{{this.$route.query.name}}</div>
            <!-- 右边 -->
            <div class="her3" @click="$router.push('/Signin')" v-if="this.$store.state.val=='' ">
                <span>登录</span>
                <span>|</span>
                <span>注册</span>
                
            </div>
            <div class="her3" @click="$router.push('/My')" v-if="this.$store.state.val!='' " ><van-icon name="friends-o" /></div>
        </div>
        
        <div v-if="FlagSearch" class="con">
            <!-- 左边 -->
            <div class="her1" @click="$router.push('/Address')">
                <van-icon name="arrow-left" />
            </div>
            <!-- 中间 -->
            <div class="her4">搜索</div>
            <!-- 右边 -->
            <div class="her3">
               
            </div>
        </div>
        <div v-if="FlagOrder" class="con">
            <!-- 左边 -->
            <div class="her1" @click="$router.push('/Homepage')">
                <van-icon name="arrow-left" />
            </div>
            <!-- 中间 -->
            <div class="her4">订单列表</div>
            <!-- 右边 -->
            <div class="her3">
               
            </div>
        </div>
        <div v-if="FlagMy" class="con">
            <!-- 左边 -->
            <div class="her1" @click="$router.push('/Homepage')">
                <van-icon name="arrow-left" />
            </div>
            <!-- 中间 -->
            <div class="her4">我的</div>
            <!-- 右边 -->
            <div class="her3">
               
            </div>
        </div>

        
  </div>
</template>

<script>
export default {
    props: ["FlagCity", "Flagadd", "FlagHome","FlagSearch","FlagOrder","FlagMy"],
    name: "HelloWorld",
    data() {
        return {
        msg: ""
        };
    },
    created() {},
    methods: {},
    mounted() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
#her {
    width: 100%;
    background: #3792e5;
    position: absolute;
    top: 0;
    z-index: 5;
    // display: flex;
    .con {
        width: 100%;
        display: flex;
        line-height: 2rem;
        position: relative;
        
        .her1 {
            margin-left: 5%;
            color: white;
            i {
                line-height: 2rem;
            }
        }
        .her2 {
            color: white;
            // margin-left: 4rem;
        }
        .her3 {
            position: absolute;
            right: 5%;
            color: white;
            span {
                margin-left: 0.2rem;
            }
        }
        .her4{
            color: white;
            margin-left: 5.5rem;
           
         }
    }
}
</style>



