<template>
  <div id="her">
       <div v-if="FlagAcc" class="con">
            <!-- 左边 -->
            <div class="her1" @click="$router.push('/My')">
                <van-icon name="arrow-left" />
            </div>
            <!-- 中间 -->
            <div class="her4">我的</div>
            <!-- 右边 -->
            <div class="her3">
               
            </div>
        </div> 
        <div v-if="FlagSignin" class="con">
            <!-- 左边 -->
            <div class="her1" @click="$router.go('-1')">
                <van-icon name="arrow-left" />
            </div>
            <!-- 中间 -->
            <div class="her4">密码登录</div>
            <!-- 右边 -->
            <div class="her3">
               
            </div>
        </div>
        <div v-if="FlagEdit" class="con">
            <!-- 左边 -->
            <div class="her1" @click="$router.push('/Account')">
                <van-icon name="arrow-left" />
            </div>
            <!-- 中间 -->
            <div class="her4">编辑地址</div>
            <!-- 右边 -->
            <div class="her3" @click="fn()" v-if="arr" >
               编辑
            </div>
            <div class="her3" @click="fnn()"  v-if="arr2">
               完成
            </div>
        </div>
         <div v-if="FlagAdded" class="con">
            <!-- 左边 -->
            <div class="her1" @click="$router.push('/Edit')">
                <van-icon name="arrow-left" />
            </div>
            <!-- 中间 -->
            <div class="her4">新增地址</div>
            <!-- 右边 -->
            <div class="her3">
               
            </div>
        </div>
        <div v-if="FlagNew" class="con">
            <!-- 左边 -->
            <div class="her1" @click="$router.go('-1')">
                <van-icon name="arrow-left" />
            </div>
            <!-- 中间 -->
            <div class="her4">搜索地址</div>
            <!-- 右边 -->
            <div class="her3">
               
            </div>
        </div>
  </div>
</template>

<script>
export default {
    props: ["del","del2","FlagAcc","FlagSignin","FlagEdit","FlagAdded","FlagNew"],
    name: "HelloWorld",
    data() {
        return {
        msg: "",
        arr:true,
        arr2:false
        };
    },
    created() {},
    methods: {
       fn(){
           if(this.del){
               this.del()
           }
           this.arr2=true
           this.arr=false
       },
       fnn(){
           if(this.del2){
               this.del2()
           }
           this.arr2=false
           this.arr=true 
       }
    },
    mounted() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
#her {
    width: 100%;
    background: #3792e5;
    position: absolute;
    top: 0;
    z-index: 5;
    display: flex;
    .con {
        width: 100%;
        display: flex;
        line-height: 2rem;
        position: relative;
        .her1 {
            margin-left: 5%;
            color: white;
            i {
                line-height: 2rem;
            }
        }
        .her2 {
            color: white;
            margin-left: 3rem;
        }
        .her3 {
            position: absolute;
            right: 5%;
            color: white;
            span {
                margin-left: 0.2rem;
            }
        }
        .her4{
            color: white;
            margin-left: 5.5rem;
        }
    }
}
</style>



